    margin-left: 5vw;
  }
  .second {
    width: 100%;
    margin-left: 5vw;
    padding-left: 2.2rem;
    padding-top: 1.5rem;
  }
  .building {
    width: 130vw;
    margin-top: 7rem;
  }
  .event-div {
    width: 100%;
    margin-top: 0;
    display: flex;
    flex-direction: column-reverse;
    margin-left: 5vw;
    padding-left: 1.8rem;
    transform: translateY(-10%);
  }
}

@media only screen and (max-width: 650px) {
  .leftDiv {
    margin-left: 0;
  }
  .events-title {
    text-align: center;
    margin-bottom: 4rem;
    margin-top: 3.5rem;
  }
  .buttons {
    padding-bottom: 0;
    margin-left: 0;
    display: flex;
    flex-flow: column;
    align-items: center;
  }
  .event-div {
    margin-top: 0;
    margin-left: 0;
    padding-left: 0;
  }
  .slide-div {
    margin-left: 0;
    padding-bottom: 0;
    display: flex;
    flex-flow: column;
    align-items: center;
  }
  .second {
    width: 100%;
    padding-left: 0;
    margin-left: 0;
    display: flex;
    flex-flow: column;
    align-items: center;
  }
  #grid-buttons {
    transform: translate(25%, 28%);
  }
}
@media screen and (max-width: 380px) {
  :root {
    --size-divisor: 2;
    --div-divisor: 2.6;
  }
  #grid-buttons {
    transform: translate(15%, 28%);
  }
}

.faq__section {
  width: 100vw !important;
  font-family: "Raleway", sans-serif;
  background: #0d0265;
  background: linear-gradient(to bottom, #0d0265, #0e5896);
  display: flex;
  flex-direction: column;
  /* padding-bottom: 8rem; */
}
.faq__section__mobile {
  height: 45vh;
}
.faq__section__laptop {
  height: 70vh;
}

.faq__title {
  width: 100%;
  padding: 50px;
  height: 15%;
  text-align: center;
  font-size: 3rem;
  color: white;
}

.faq__bottom__div {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: space-between;
}
.faq__left__button {
  height: 100%;
  width: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  z-index: 10;
}
.left__button {
  max-width: 80%;
  height: auto;
  margin-left: 20px;
}
.faq__questions__div {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 80%;
}
.full__question__box {
  height: 49%;
  width: 48%;
  background-color: #e9e9e9;
  margin-top: 1rem;
  border-radius: 20px;
}
.full__question__box__large {
  height: 100%;
  width: 80%;
  background-color: #e9e9e9;
  border-radius: 20px;
}
.question__div {
  height: 38%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 10px;
  text-align: center;
  font-size: 1.3rem;
  background-color: white;
  border-radius: 20px;
}
.answer__div {
  display: flex;
  padding: 5px 20px;
  width: 100%;
  height: 60%;
  font-size: 1rem;

  overflow-x: hidden;
  overflow-y: scroll;
  opacity: 1;
  border-radius: 20px;
}
/* ::-webkit-scrollbar {
  width: 0;
} */

.faq__right__button {
  height: 100%;
  width: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.right__button {
  max-width: 80%;
  height: auto;
  margin-right: 20px;
}

@media only screen and (max-width: 820px) {
  .faq__section {
    padding-bottom: 0;
  }
}

@media only screen and (max-width: 650px) {
  .faq__title {
    font-size: 1.5rem;
  }
  .question__div {
    font-size: 1rem;
  }

  .faq__bottom__div {
    margin-top: -1em;
  }
}

