    margin-bottom: 30px;
    /* left: 50%; */
    /* transform: translateX(-50%); */
  }

  .reg-back-btn {
    font-size: 1.5rem;
    margin-left: 2rem;
    position: relative;
    top: 3.5rem;
    z-index: 15;
    color: white;
  }

  .sign-up {
    position: relative;

    left: 50%;
    margin-top: 25px;
    transform: translateX(-50%);
    background-color: white;
    height: 40px;
    width: 110px;
    border-radius: 49px;

    border: none;
  }

  .label-register-form {
    color: white;
    margin-top: 17px;
    font-size: 1.1rem;
  }

  .registration-form > input,
  .registration-form > select {
    height: 6vh;
    border-radius: 30px;
  }
}

.reg-sec {
  font-family: "Raleway", sans-serif;
  width: 100vw !important;
  height: 100vh;
  color: white;
  background: linear-gradient(to top, #62276e, #883660);
  z-index: -3;
}
.building-reg {
  width: 80vw;
  position: absolute;
  right: 0;
  z-index: 0;
  bottom: 0;
  /* margin-top: 3rem; */
  transform: translate(25%);
}
.logo {
  margin-top: 0rem;
  width: calc(30rem / 1);
  width: calc(30rem / var(--size-div));
}
.main-div {
  display: flex;
  flex-flow: column;
  align-items: center;
}
.heading {
  background-color: white;
  color: black;
  text-align: center;
  margin-top: 2rem;
  padding: 10px 50px 10px 50px;
  border-radius: 50px;
  position: relative;
  font-size: calc(3rem / 1);
  font-size: calc(3rem / var(--size-div));
}

.user-id {
  font-size: 1.2rem;
  background-color: white;
  color: black;
  padding: 20px 50px;
  border-radius: 40px;
}

.timer-div {
  background-color: #d7c2d1;
  text-align: center;
  border-radius: 50px;
  font-size: 1.5rem;
  margin-top: calc(5rem / 1);
  margin-top: calc(5rem / var(--size-div));
  color: black;
  padding: 20px 50px 20px 40px;
  display: flex;
  flex-flow: column;
  align-items: center;
  transform: translateY(1%);
}
.circle-div {
  margin-top: 10px;
  color: black;
  align-content: center;
  background-color: white;
  align-items: center;
  border-radius: 50%;
  width: 5rem;
  height: 5rem;
  padding: 1.3rem 1.8rem;
  font-family: "Raleway", sans-serif;
  margin-bottom: 20px;
}

.circle-div p {
  font-family: "Raleway", sans-serif;
}

.cc-div {
  font-family: "Raleway", sans-serif;
  display: flex;
  flex-flow: column;
  align-items: center;
  padding: 0 10px;
}
.timer-div-title {
  font-size: 1.7rem;
  font-family: "Raleway", sans-serif;
}
h1 {
  text-align: center;
  /* font-family: "Raleway", sans-serif; */
}
.back-button {
  font-size: 3rem;
  color: white;
  margin-left: 3rem;
  position: relative;
  top: 3rem;
}

:root {
  --size-div: 1;
}

@media screen and (max-width: 761px) {
  :root {
    --size-div: 1.5;
  }
  .building-reg {
    width: 130vw;
    margin-top: 5rem;
  }
}

@media screen and (max-width: 480px) {
  :root {
    --size-div: 2.3;
  }
  .building-reg {
    width: 150vw;
    margin-top: 7rem;
    transform: translate(30%);
  }
  .heading {
    margin-top: 1.5rem;
    padding: 8px 20px 8px 20px;
    border-radius: 50px;
    position: relative;
    font-size: calc(3rem / 1);
    font-size: calc(3rem / var(--size-div));
  }
  .timer-div-title {
    font-size: 1rem;
  }

  .user-id {
    font-size: 0.8rem;
    background-color: white;
    color: black;
    padding: 10px 30px;
    border-radius: 40px;
  }

  .timer-div {
    border-radius: 40px;
    font-size: 1rem;
    padding: 10px 30px 10px 30px;
  }
  .circle-div {
    margin-top: 10px;
    width: 3rem;
    height: 3rem;
    padding: 0.6rem 1rem;
    margin-bottom: 10px;
  }
  .cc-div {
    padding: 0 4px;
  }
  h5 {
    font-size: 0.7rem;
  }
  .back-button {
    font-size: 2rem;
    margin-left: 1rem;
  }
}

.participants-div {
  min-height: 100vh;
  background: rgb(80, 79, 79) !important;
  color: white;
  font-family: "Raleway", sans-serif;
  padding-top: 2rem;
}

.participants-div p {
  display: flex;
  justify-content: center;
