.allusers {
  min-height: 100vh;
  background: rgb(80, 79, 79) !important;
  color: white;
  font-family: "Raleway", sans-serif;
  padding-top: 2rem;
  width: 100vw;
}

.allusers p {
  display: flex;
  justify-content: center;
  font-size: 1.5rem;
}

